.process-section{
    background-color: #F6F6F6;
    margin-top: 50px;
}


.process-section h1{
    font-size: 1.9rem;
    padding: 50px 0px 0px 0px;
    text-align: center;
    color: var(--color-prymary);

}

.process-container{
    display:flex;
    flex-wrap: wrap;
    padding: 100px;
    justify-content: center;
    gap: 100px;
}

.process {
    width: 260px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.process h1 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 70px;
    color: var(--color-prymary);
    font-weight: bold;
}

.process img {
    width: 60%;  
    margin-bottom: 30px;
}

.process h3 {
    color: var(--color-prymary);
    font-size: 1.5rem;
    font-weight: bold;
    height: 50px;
    margin-bottom: 30px;
    text-align: center;
}

.process p{
    text-align: center;
    display: flex;
    align-items: center;
    height: 100px;
}

.line {
    width: 250px;
    height: 2px;
    background-color: #777272;
    margin-bottom: 30px;
}

.process-container-img{
    height: 180px;
    display: flex;
    justify-content: center;
}

@media (max-width: 1550px){
    .process{
        width: 220px;
    }   
}
@media(max-width:1380px){
    .process-container{
        gap: 100px 150px;
        margin: 0px 50px;
    }
    .process{
        width: 300px;
    }

    .process p{
        font-size: 1.2rem;
    }

}   


@media(max-width: 500px){

    .process-section h1{
        margin-bottom: 35px;
    }

    .process-container{
        gap: 30px;
        margin: 0px;
        padding: 40px 20px;
    }
    .process{
        width: 44%;
        gap: 0px
    }

    .process img{
        height: 70%;
    }

    .process h3{
        font-size: 1.1rem;
        margin-bottom: 15px;
    }

    .process p{
        font-size: 1rem;
    }

    .line{
        display: none;
    }
}