

.faixa-nav{
    background-color: var(--color-prymary);
    height: 50px;
    color: white;
    text-align: center;
    line-height: 50px;
    width: 100%;
    font-size: 1.2rem;
    font-weight: 500 ;
    margin-top: 0px;
    transition: transform 0.5s ease-in-out; /* Adicionando uma transição suave */
    transform: translateY(0);
}

@media(max-width:500px){
    .faixa-nav{
        display: none;
    }
}

