.products-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 80px;
}

.products-container h1{
    font-size: 1.9rem;
    font-weight: 600;
    margin-bottom: 30px;
}
.products-container h2{
    font-size: 1.7rem;
    margin-bottom: 50px;
    font-weight: 500;
    color: var(--color-prymary);
}

.products{
    display: flex;
    flex-wrap: wrap;
    gap: 80px 90px;
    align-items: center;
    justify-content: center;
    padding: 0px 100px;
}


.card-content{
    width: 280px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    color: black;
    cursor: pointer;
}

.card-content img{
    margin-bottom: 15px;
    height: 225px;
    transition: transform 0.3s;
    z-index: -10;

}

.card-content:hover img {
    transform: scale(1.07);
     /* Aumenta a escala da imagem quando .card-content estiver em hover */
  }

.card-content h6{
    margin-top: 10px;
    font-size: 1rem;
}

.card-content button {
    border-radius: 5px;
    width: 200px;
    height: 40px;
    font-size: 1.1rem;
    background-color: white;
    font-weight: 500;
    margin-top: 25px;
    color: var(--color-prymary);
    border: #5cac91 solid 2px;
    cursor: pointer;
}

.card-content:hover button{
    background-color: #5cac91;
    color: white;
}



.top-sales{
    margin: 60px 0px 60px 0px;
    font-weight: 700;
    font-size: 1.9rem;
    color: var(--color-prymary);
}

@media(min-width: 1580px){
    .card-content{
        width: 330px;
    }
    .products-container{
        padding: 0px 100px;
    }    
}

@media (max-width: 1300px){
    .products{
        gap: 70px;
    }

    .card-content{
        width: 230px;
    }
}


@media(max-width: 500px){
    .products-container h1 {
        font-size: 1.4rem;
    }
    .products-container h2{
        font-size: 1.3rem;
    }

    .products{
        padding: 0px;
        gap: 40px;
    }
    
    .products a{
        max-width: 140px;
    }

    .card-content{
        width: auto;
    }

    .card-content button{
        width: 100%;
    }

    .card-content img{
        height: 130px;
    }

    .card-content p{
        font-size: .9rem;
    }
}
