.product-container{
    display: flex;
    gap: 40px;
    width: 55%;
}

.splide{
    width: 50%;
    z-index: 0 !important;
}

.splide__slide img {
    width: 100%;
    height: 100%;
    margin-top: 50px;
    border-radius: 5px;
    
  }

  /* Altera a cor de fundo dos indicadores */
.splide__pagination .splide__pagination__page.is-active {
    background-color: var(--color-prymary) /* Substitua com a cor desejada */
  }
  
  .splide__pagination .splide__pagination__page:not(.is-active) button {
    background-color: #0000ff; /* Substitua com a cor desejada */
  }

  .splide__pagination{
    position: absolute;
    bottom: -30px;
  }

  @media(max-width: 500px){
    .product-container{
      flex-direction: column;
      width: 100%;
      margin-bottom: 80px;
      align-items: center;
    }

    .splide{
      width: 80%;
    }
    .splide__slide img{
      margin-top: 75px;
    }
  }

  
  
  