.categories {
    width: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.categories h1 {
    margin-bottom: 30px;
    font-size: 1.7rem;
    color: #555050;
}

.categories details {
    font-size: 20px;
    font-weight: bold;
    width: 300px;
    line-height: normal;
    border-radius: 8px;
}

.categories details summary {
    background-color: #ffffff;
    padding: 4%;
    color: #59A47F;
    border: solid 1px #59A47F;
    border-radius: 2px;
    height: 27px;
    margin-bottom: 10px;
    text-align: center;
}

.categories details summary:hover {
    color: #32684d;
    border: solid 1px #32684d;
}

.categories details p {
    background-color: #ffffff;
    color: #59A47F;
    font-size: 1rem;
    text-align: center;
    padding: 5%;
    width: 90%;
    height: 15px;
    border-bottom: solid 1px gray;
    margin-top: 2px;
    border-radius: 4px;
    margin-bottom: 10px;
}

.categories details p:hover {
    color: #32684d;
}

@media(max-width: 500px){
.categories{
    display: none;
}
}