.feedbacks{
    padding: 90px 100px ;
    display: flex;
    flex-direction: column;
    gap: 20px;
    background-color: #F6F6F6;
}

.feedbacks h1{
    font-size: 1.8rem;
    color: var(--color-prymary);
}

.feedbacks form{
    display: flex;
    flex-direction: column;
}

.without-feedbacks{
    width: calc(100% - 20px);
    background-color: #e2e2e2;
    height: 40px;
    display: flex;
    padding-left: 20px;
    align-items: center;
}

.feedbacks input, .feedbacks textarea{

    height: 50px;
    font-size: 1rem;
    border: none;
    outline: none;
    padding-left: 15px;
}

.feedbacks input{
    margin: 20px 0px 0px 0px;
    width: 40%;
}

.feedbacks textarea{
    height: 100px;
    padding-top: 10px;
    width: calc(100% - 15px);
}

.feedbacks button{
    border-radius: 5px;
    width: 200px;
    height: 40px;
    margin-bottom: 20px;
    font-size: 1.1rem;
    background-color: white;
    font-weight: 500;
    margin-top: 25px;
    color: var(--color-prymary);
    border: #5cac91 solid 2px;
    cursor: pointer;
}

.feedbacks button:hover{
    background-color: #5cac91;
    color: white;
}

@media(max-width:500px){
    .feedbacks{
        padding: 40px 50px;
    }
    .feedbacks input{
        width: 95%;
    }
}