.description{
    padding: 150px 100px 50px 100px;
}

.description p{
    font-size: 1.1rem;
}

.description .line, .kit-description .line{
    width: 100%;
    margin-top: 10px;
}

.kit-description{
    padding: 0px 100px 100px 100px;
}

.kit-description p{
    font-size: 1.1rem;
}

.description h1, .kit-description h1{
    color: var(--color-prymary);
}

@media(max-width:500px){
    .kit-description, .description{
        padding: 0px 40px 40px 40px;
    }

    .kit-description p, .description p{
        font-size: .95rem;
    }    
}
