.login-container {
    max-width: 350px;
    width: 280px;
    padding: 20px;
    position:absolute;
    top: 130px;
    pointer-events: all;
    right: 8%;
    border: 1px solid #acaaaa;
    border-radius: 5px;
    background-color: white;
    align-items: baseline;
    z-index: 100;
    display: block;
  }

  .login-container-header{
    display: flex;
  }

  .close-form-login{
    border: none !important;
    width: 30px !important;
    height: 30px !important;
    position: absolute;
    right: 15px;
    top: -10px;
    background: none;
    padding: 0;
    margin: 0;
  }

  .close-form-login:hover{
    background-color: rgb(214, 206, 206) ;
  }
  
  #login-form button {
    width: 40%;
  }

 .login-container [type="email"],
 .login-container input[type="password"], .login-container input[type="text"] {
  display: block;
  width: 90%;
  margin: 15px 0px;
  padding: 8px;
  outline: none;
  border: none;
  border-bottom: 1px solid;
}

.login-container button, .login-container [type="submit"] {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  width: 240px;
  cursor: pointer;
  justify-content: center;
  gap: 10px;
  border: 1px solid rgb(197, 196, 196);
  padding: 0px 10px;
  margin-top: 15px;
  height: 35px;
  font-size: .95rem;
  border-radius: 5px;
}

.login-container button:hover, .login-container [type="submit"]:hover, #voltar:hover{
    background-color: rgb(243, 243, 243);
}

#voltar, .login-container [type="submit"] {
  margin-top: 15px;
  width: 100px;
  height: 30px;
  font-size: 1rem;
  color: var(--color-prymary);
  background-color: white;
}

#voltar{
  margin-bottom: -5px !important;
}

.login-container button i {
  color: #3b5998;
  font-size: 1.3rem;
  margin-right: 15px;
}

.login-container a{
    cursor: pointer;
}

.login-container [type="submit"]{
    margin: 20px 0px 15px 0px;
}

#logo-google {
  height: 20px;
  margin-right: 15px;
}


.login-container.hidden {
    display: none;
  }
  
  .login-container.visible {
    display: block;
  }

  #register{
    text-decoration: underline;
    text-underline-offset: 2px;
    cursor: pointer;
  }

  .input-password{
    display: flex;
    align-items: center;
  }

  .password-eye, .password-eye-slash{
    position: absolute;
    right: 50px;
  }