.footer {
    height: 280px;
    background-color: var(--color-prymary);
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    color: white;
    font-weight: 300;
    font-size: 1rem;
    padding: 0px 30px;
  }

.footer-container2{
  display: flex;
  gap: 170px;
  align-items: center;
  justify-content: center;
 }


 .footer-container1{
  margin-bottom: 50px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center
 }


 .footer-container2 p{
  margin-bottom: 20px;
 }
 
  #delivery-by-whatsapp {
    position: fixed;
    bottom: 0;
    right: 20px;
    height: 95px;
  }

  .payment-methods{
    display: flex;
    flex-wrap: wrap;
  }

  .payment-methods svg{
    height: 23px;
  }

  .payment-methods svg:hover{
    transform: scale(1.2); 
  }

  .social-medias ul {
    gap: 20px;
    display: flex;
  }

  .social-medias ul li {
    list-style: none;
  }

  .icons-footer{
    height: 25px;
  }

  .copyright{
    margin-top: 60px;
  }

  .icons-footer:hover{
    transform: scale(1.1); 
    cursor: pointer;
  }

  @media(max-width: 500px){
    .footer{
      height: auto;
      padding: 40px 30px;
    }
    .footer-container2{
      gap: 40px;
      align-items: baseline;
      flex-direction: column;
    }
    .copyright{
      margin-top: 40px;
    }
    #delivery-by-whatsapp{
      height: 55px;
    } 
  }