.header {
    width: 100%;
    font-family: roboto;
    z-index: 100;
}


.header-container {
    transition: transform 0.4s ease-in-out;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 100;
    pointer-events: none;

}

.scrolling-up {
    transform: translateY(-50px);
}

.scrolling-up .sidebar {
    transform: translateY(50px);
    transition: transform 0.4s ease-in-out;
}

.nav {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    pointer-events: all !important;
    background-color: white;
    width: 100%;
    gap: 7%;
    box-shadow: 0 0.5rem 1rem #7a7a7a;
}

.nav ul {
    display: flex;
    gap: 80px;
    margin-left: -20px;
    align-items: center;

}

.nav ul li {
    list-style: none;
    color: black;
    font-size: 1.2rem;

}

#promotions {
    background-color: var(--color-prymary);
    height: 40px;
    width: 140px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    color: white;
    font-weight: bolder;
}

.nav a {
    text-decoration: none;
}

#logo-marmitaria {
    height: 80px;
}

.logo-header-responsive{
    display: none;
}

.container-bi-icons {
    margin-left: 0px;
    display: flex;
}

.bi-icons {
    height: 21px;
}

#icon-search {
    position: absolute;
    right: 10px;
    top: 14px;
    width: 16px;
    cursor: pointer;
}

.img-cart {
    margin-top: 5px;
    height: 24px;
    cursor: pointer;
}

.container-img {
    height: 100%;
    width: 40px;
    margin: 5px 20px 0px 130px;
}

.cart-itens {
    position: absolute;
    height: 15px;
    top: 20px;
    margin-left: 15px;
    font-size: 11px;
    text-align: center;
    color: white;
    font-weight: bold;
    background-color: red;
    width: 15px;
    border-radius: 50%;
}

#user {
    height: 22.1px;
    margin-top: 7px;
    margin-right: 150px;
}

.input-wrapper {
    width: 230px;
    display: flex;
    margin-right: -80px;
    position: relative;
}

.search-input{
    border: none;
    border-bottom: 1px solid rgb(0, 0, 0);
    outline: none;
    display: block;
    width: 230px;
    font-size: 1.2rem;
    margin: 15px 20px 7px 0px;
    transition: border-bottom 0.5s;
}

.labelInput {
    font-size: 1rem;
    position: absolute;
    transform: translateY(14px);
    transition: 0.5s;
    pointer-events: none;
}

.search-input:focus+.labelInput,
.search-input:valid+.labelInput {
    font-size: 0.6rem;
    transform: translateY(0px);
    color: rgb(0, 0, 0);
}

.btn-login-form {
    background: none;
    border: none;
    cursor: pointer;
    width: 0px;    
    margin-right: 110px;
}

@media(max-width: 1300px){
    #user{
        margin-right: 90px;
    }

    .container-img{
        margin-left: 40px;
    }
}

@media(max-width:500px){
    .nav{
        flex-direction: column-reverse;

    }

    .nav ul{
        display: none;
    }

    .container-bi-icons{
        align-items: center;
        width: 100%;
        justify-content: space-between;
        padding: 0px 0px 10px 40px;
        border-bottom: 1px solid rgb(217, 217, 217);
    }

    #logo-marmitaria{
        display: none;
    }

    .logo-header-responsive{
        display: block;
        height: 45px;
        margin-right: 30px;
    }

    .form-group{
        width: 75%;
        margin: 5px 0px;
        
    }

    .search-input, .input-wrapper{
        width: 100%;
        border: none;
        color: rgb(111, 111, 111);
    }
}   