.side-cart {
  position: fixed;
  top: 0;
  width: 430px;
  right: 0;
  z-index: 1000;
  height: calc(100% + 50px);
  background-color: #fff;
  z-index: 1000;
  transition: transform 0.4s ease-in-out !important;
  box-shadow: 0px 0px 10px 0px #444343;
  transform: translateX(100%); /* Inicialmente, o carrinho está fora da tela */
}

.side-cart.opened {
  transform: translateX(0px);
  pointer-events: all; /* Quando aberto, o carrinho é movido para a tela com transição */
}

.sidebar h1 {
  color: var(--color-prymary);
  font-weight: 500;
}

.sidebar p {
  text-align: center;
}

.sidebar {
  transition: transform 0.4s ease-in-out !important;
  display: flex;
  height: 90%;
  padding: 15px;
  z-index: 100;
  gap: 10px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.sidebar .line{
  position: absolute;
  top: 70px;
  width: 100%;
  height: 1px;
  background-color: rgb(238, 238, 238);
}

.sidebar button {
  border-radius: 10px;
  width: 230px;
  height: 52px;
  margin: 5px;
  background-color: white;
  color: var(--color-prymary);
  font-size: 1.3rem;
  font-weight: 400;
  border: #598670 solid 1px;
  cursor: pointer;
}

.sidebar button:hover{
  color: white;
  background-color:var(--color-prymary); ;
}

.close-cart{
  position: absolute;
  top: 30px;
  right: 20px;
  cursor: pointer;
  }

  .close-cart:hover .svg-close-sidecart{
   fill: gray;
  }
