.banner-img {
    margin-top: 80px;
    width: 100%;
}

.banner {
    display: flex;
    align-items: center;
    padding: 0px 100px;
    height: 70px;
    background-color: rgb(247, 247, 247);
    justify-content: center;
}

.banner ul {
    display: flex;
}

.banner li {
    list-style: none;
    display: flex;
    align-items: center;
    gap: 35px;
    font-size: 1.1rem;
    margin-left: 35px;
}


.banner-icons {
    height: 40px;
}

@media(max-width: 500px) {
    .banner {
        max-width: 100%;
        height: auto;
        padding: 0px 20px;
        background-color: white;
    }

    .container-img-home{
        width: 100%;
        display: flex;
        align-items: center;
        margin-bottom: 50px;
        justify-content: center;
    }

    .banner-img{
        width: 90%;
    }

    .banner-img{
        margin-top: 130px;
        border-radius: 10px;
        
    }   

    .banner ul{
        display: flex;
        flex-wrap: wrap;
        gap: 30px 30px;
        justify-content: center;
        
    }

    .banner-icons {
        height: 37px;
        color: rgba(70, 190, 90, 0.349);
    }


    .banner li {
        display: flex;
        flex-direction: column;
        text-align: center;
        font-size: .9rem;
        width: 40%;
        margin: 0;
    }

}