.product-price {
    width: 50%;
    margin-top: 40px;
}

.product-price h3 {
    color: var(--color-prymary);
    font-size: 1.8rem;
    margin-bottom: 40px;
}

.product-price h6{
    font-size: 1.5rem;
    margin-bottom: 15px;
}

.ingredients p{
    margin-bottom: 45px;
}

.pre-price{
    font-size: 1rem;
    text-decoration: line-through;
}
.post-price{
    font-size: 1.8rem;
    font-weight: 500;
}
.add-remove {
    display: flex;
    width: 130px;
    height: 45px;
    border: 2px solid #5cac91;
    border-radius: 5px;
    margin-top: 20px;
}

.add-remove p {
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    width: 33.3%;
    font-weight: 600;
    height: 101%;
    border: 1px solid #5cac91;
    border-top: none; 
}

.add-remove button {
    height:  100%;
    width: 33.33333%;
    font-weight: 600;
    background-color: white;
    border: none;
}

.remove{
    border-radius: 3px 0px 0px 3px;
}

.add{
    border-radius: 0px 3px 3px 0px;
}

.add-remove button:hover{
    background-color: #5cac91;
    color: white;
}

.container-add-item{
    display: flex;
    align-items: center;
}

.btn-add-to-cart{
    gap: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 47px;
    margin-top: 20px;
    margin-left: 30px;
    width: 55%;
    border: none;
    padding: 10px;
    font-size: 1rem;
    color: white;
    background-color: var(--color-prymary);
    border-radius: 5px;
    cursor: pointer;
}

.btn-add-to-cart:hover{
    background-color: #649b89;
}


@media(max-width:500px){
    .product-price{
        width: 80%;
        margin-top: 30px;
    }
    .product-price h3{
        position: absolute;
        top: 145px; 
        padding-right: 15px;
        font-size: 1.7rem;
    }
}